import { useContext, useState } from 'react'
import { AuthContext } from '../auth/use-auth'
import ProfileHeader from './profile-header'

import LoadingButton from '@web/src/components/ui/loading-button'
import type { MainResult } from '@web/src/scripts/fill'
import * as sonner from 'sonner'

import * as Sentry from '@sentry/react'
import { trpcReact } from '@web/src/api/trpc'
import { Button } from '@web/src/components/ui/button'
import { useStripe } from '@web/src/lib/useStripe'

const Auto = () => {
	const [isLoading, setIsLoading] = useState(false)

	const { user } = useContext(AuthContext)

	const { openCheckout } = useStripe()
	const trpcUtils = trpcReact.useUtils()

	const { logout } = useContext(AuthContext)

	const { data: availableForms } =
		trpcReact.form.getAvailableFormsToSolve.useQuery()

	const handleStart = (): void => {
		chrome.tabs.query({ active: true, currentWindow: true }, (tabs) => {
			const tab = tabs[0]
			if (!tab?.url) return

			const isSupported = tab.url.includes('docs.google.com/forms')

			if (!isSupported) {
				return sonner.toast('Please go to the Google Forms page')
			}

			for (const tab of tabs) {
				if (!tab?.active || !tab?.id) {
					continue
				}
				const port = chrome.tabs.connect(tab.id, {
					name: 'auto',
				})

				setIsLoading(true)

				port.onMessage.addListener((message: MainResult) => {
					setIsLoading(false)

					if (message.status === 'SUCCESS') {
						trpcUtils.form.getAvailableFormsToSolve.invalidate()
					}

					if (message.status === 'UNEXPECTED_ERROR') {
						sonner.toast('Error', {
							description: 'Something went wrong',
						})

						Sentry.captureEvent({
							message: 'Unexpected error',
							tags: {
								url: tab.url,
							},
							extra: {
								cause: message.cause,
							},
							user,
						})
					}

					if (message.status === 'ERROR') {
						if (message.cause.data?.code === 'UNAUTHORIZED') {
							return logout()
						}

						let action: sonner.ToastT['action'] | undefined

						if (message.cause.data?.code === 'TOO_MANY_REQUESTS') {
							action = {
								label: 'Upgrade',
								onClick: openCheckout,
							}
						}

						if (
							!['UNAUTHORIZED', 'TOO_MANY_REQUESTS'].includes(
								message.cause.data?.code ?? '',
							)
						) {
							Sentry.captureEvent({
								message: message.cause.shape?.message ?? 'Unexpected error',
								extra: {
									cause: message.cause,
								},
								tags: {
									url: tab.url,
								},
								user,
							})
						}

						if (message.cause.shape)
							sonner.toast('Error', {
								description:
									message.cause.shape?.message ?? 'Something went wrong',
								action,
							})
					}
				})
			}
		})
	}

	const canSolveMoreForms =
		user.tier !== 'FREE' || (availableForms && availableForms > 0)

	return (
		<div className='h-dvh bg-background flex flex-col relative'>
			<ProfileHeader />
			<div className='flex-1 flex justify-center items-center flex-col'>
				{user.tier !== 'FREE' ? null : canSolveMoreForms ? (
					<p className='mb-4 text-sm'>
						You can solve {availableForms} forms for free
					</p>
				) : (
					<p className='mb-4 text-sm'>
						You have reached the limit for free users
					</p>
				)}

				{canSolveMoreForms ? (
					<LoadingButton
						size='lg'
						onClick={handleStart}
						disabled={!canSolveMoreForms}
						isLoading={isLoading}
					>
						Start
					</LoadingButton>
				) : (
					<Button
						onClick={openCheckout}
						className='relative overflow-hidden text-white font-bold py-3 px-6 bg-gradient-to-r from-purple-500 via-pink-500 to-red-500  hover:from-purple-600 hover:via-pink-600 hover:to-red-600 transition-all duration-1000 hover:shadow-lg'
					>
						Upgrade
					</Button>
				)}
			</div>
		</div>
	)
}

export default Auto
