import React, { useState } from 'react'

export function useStorageToken() {
	const [token, setToken] = React.useState<string | null>(null)

	const [sub, setSub] = useState<string>()

	React.useEffect(() => {
		const getToken = async () => {
			const data = await chrome.storage.local.get(['token', 'sub'])

			if (!data.token) return
			setToken(data.token)

			setSub(data.sub)
		}

		getToken()
	}, [])

	const removeToken = async () => {
		setToken(null)
		await chrome.storage.local.set({ token: null })
	}

	const setStorageSub = (sub: string) => {
		chrome.storage.local.set({ sub })
		setSub(sub)
	}

	return { token, removeToken, setToken, sub, setSub: setStorageSub }
}
