import { User } from '@server/user/user.schema'
import React, { useState } from 'react'

export const AuthContext = React.createContext<{
	user: User
	logout: () => Promise<void>
}>({
	user: {
		sub: '',
		familyName: '',
		givenName: '',
		name: '',
		picture: '',
		email: '',
		createdAt: '',
		tier: 'FREE',
		customerId: '',
		language: '',
		countryCode: '',
		country: '',
	},
	logout: async () => {},
})

export function useAuthContext() {
	return React.useContext(AuthContext)
}

export function useLogin({
	onSuccess,
}: {
	onSuccess: (token: string) => void
}) {
	const [isLoading, setIsLoading] = useState(false)

	return {
		login: async () => {
			setIsLoading(true)
			const token = await chrome.runtime.sendMessage({ action: 'login' })
			setIsLoading(false)
			onSuccess(token)
		},
		isLoading,
	}
}
