import { trpcReact } from '../api/trpc'

export const useStripe = () => {
	const createCheckoutSessionMutation =
		trpcReact.stripe.createSession.useMutation({
			onSuccess(session) {
				if (!session.url) return
				chrome.tabs.create({ url: session.url })
			},
		})

	const createPortalSessionMutation =
		trpcReact.stripe.createPortalSession.useMutation({
			onSuccess(session) {
				if (!session.url) return
				chrome.tabs.create({ url: session.url })
			},
		})

	return {
		openCheckout: () => createCheckoutSessionMutation.mutate(),
		openCustomerPortal: () => createPortalSessionMutation.mutate(),
	}
}
