import {
	MutationCache,
	QueryCache,
	QueryClient,
	QueryClientProvider,
} from '@tanstack/react-query'
import { isTRPCClientError, trpcClient, trpcReact } from './api/trpc'

import { Toaster } from './components/ui/sonner'
import Auth from './pages/auth/auth'

import { ToastT, toast } from 'sonner'
import { getSupportEmailLink } from './lib/utils'
import Auto from './pages/auto/auto'

const handleError = (error: unknown) => {
	let message = 'Something went wrong'

	if (error instanceof Error) {
		message = error.message
	}

	let action: ToastT['action'] | undefined

	if (isTRPCClientError(error)) {
		message = error.shape?.message ?? message

		const code = error.shape?.data.code

		if (code === 'UNAUTHORIZED') {
			console.log('Unauthorized')
			return
		}

		if (code === 'CONFLICT') {
			action = {
				label: 'Instructions',
				onClick: () =>
					chrome.tabs.create({
						url: 'https://support.cloudhq.net/how-to-manually-update-chrome-extensions/',
					}),
			}
		}

		if (code === 'INTERNAL_SERVER_ERROR') {
			message = 'Unexpected error'
			action = {
				label: 'Support',
				onClick: () => {
					chrome.tabs.create({ url: getSupportEmailLink() })
				},
			}
		}
	}

	toast('Error', {
		description: message,
		action,
		closeButton: true,
	})
}

const queryClient = new QueryClient({
	queryCache: new QueryCache({
		onError: handleError,
	}),
	mutationCache: new MutationCache({
		onError: handleError,
	}),
})

function App() {
	return (
		<trpcReact.Provider client={trpcClient} queryClient={queryClient}>
			<QueryClientProvider client={queryClient}>
				<div className='min-w-80 min-h-80 bg-background text-foreground w-full h-screen'>
					<Auth>
						<Auto />
					</Auth>
				</div>
				<Toaster />
			</QueryClientProvider>
		</trpcReact.Provider>
	)
}

export default App
