import { TRPCClientError } from '@trpc/client'
import { trpcReact } from '@web/src/api/trpc'
import LoadingButton from '@web/src/components/ui/loading-button'
import { Loader } from 'lucide-react'
import type { PropsWithChildren } from 'react'
import { AuthContext, useLogin } from './use-auth'
import { useStorageToken } from './useStorageToken'

import * as Sentry from '@sentry/react'

type Props = {} & PropsWithChildren

const Auth = (props: Props) => {
	const { token, removeToken, setToken, sub, setSub } = useStorageToken()

	const {
		data: user,
		isInitialLoading: isUserLoading,
		isError,
		error,
	} = trpcReact.getUser.useQuery(
		{
			// biome-ignore lint/style/noNonNullAssertion: This query will be performed when token is present because of enabled: Boolean(token),
			token: token!,
			sub,
		},
		{
			enabled: Boolean(token),
			refetchOnWindowFocus: false,
			retry: false,
			onSuccess(data) {
				Sentry.setUser({ id: data.sub, email: data.email })

				setSub(data.sub)
			},
		},
	)

	const { login, isLoading: isLoginLoading } = useLogin({
		onSuccess: setToken,
	})

	const logoutMutation = trpcReact.logout.useMutation()

	const {
		getUser: { reset: resetUser },
	} = trpcReact.useUtils()

	const logout = async () => {
		logoutMutation.mutate()
		Sentry.setUser(null)
		await removeToken()
		await resetUser()
	}

	if (isError) {
		if (error instanceof TRPCClientError) {
			if (error.data?.code === 'UNAUTHORIZED') {
				removeToken()
			}
		}
		console.log('error', error)
	}

	if (isUserLoading) {
		return (
			<div className='flex justify-center items-center flex-col h-full'>
				<Loader className='animate-spin' size={32} />
			</div>
		)
	}

	if (user)
		return (
			<AuthContext.Provider value={{ user, logout }}>
				{props.children}
			</AuthContext.Provider>
		)

	return (
		<div className='flex justify-center items-center flex-col'>
			<p className='text-center text-2xl mt-16'>Form Solver</p>

			<p className='text-center mt-8 text-slate-300'>
				Please re-open the extension after login
			</p>

			<LoadingButton
				size='lg'
				className='mt-12'
				onClick={login}
				isLoading={isLoginLoading}
			>
				Login with Google
			</LoadingButton>
		</div>
	)
}

export default Auth
