import { Button } from '@web/src/components/ui/button'
import { useStripe } from '@web/src/lib/useStripe'
import { useAuthContext } from '../auth/use-auth'
import AvatarDropdown from './avatar-dropdown'

const ProfileHeader = () => {
	const { user } = useAuthContext()

	const { openCustomerPortal } = useStripe()

	return (
		<div className='p-8 flex items-center justify-between dark bg-background fixed w-full'>
			<AvatarDropdown />

			{user.tier === 'PRO' && (
				<Button
					onClick={openCustomerPortal}
					className='ml-auto text-gray-200'
					variant='link'
				>
					Manage
				</Button>
			)}
		</div>
	)
}

export default ProfileHeader
