import {
	Avatar,
	AvatarFallback,
	AvatarImage,
} from '@web/src/components/ui/avatar'
import { Button } from '@web/src/components/ui/button'
import {
	DropdownMenu,
	DropdownMenuContent,
	DropdownMenuGroup,
	DropdownMenuItem,
	DropdownMenuLabel,
	DropdownMenuSeparator,
	DropdownMenuTrigger,
} from '@web/src/components/ui/dropdown-menu'
import { useAuthContext } from '../auth/use-auth'

import { User } from '@server/user/user.schema'
import { Badge } from '@web/src/components/ui/badge'
import { useStripe } from '@web/src/lib/useStripe'
import { getSupportEmailLink } from '@web/src/lib/utils'

const MembershipBadge = () => {
	const { user } = useAuthContext()

	const badgesByTier = {
		PRO: <Badge variant='outline'>Pro</Badge>,
		PLUS: <Badge variant='outline'>Plus</Badge>,
		FREE: <Badge variant='secondary'>Free</Badge>,
	} satisfies Record<User['tier'], React.ReactNode>

	return <span>{badgesByTier[user.tier]}</span>
}

const AvatarDropdown = () => {
	const { logout, user } = useAuthContext()

	const { openCheckout, openCustomerPortal } = useStripe()

	return (
		<div className='flex gap-2 items-center'>
			<DropdownMenu>
				<DropdownMenuTrigger asChild>
					<Button
						variant='ghost'
						className='relative rounded-full h-9 w-9 hover:bg-muted transition-all duration-200 hover:shadow-lg'
					>
						<Avatar className='h-9 w-9'>
							<AvatarImage
								src={user.picture ?? undefined}
								alt={`${user.name} avatar`}
							/>
							<AvatarFallback>
								{user.givenName?.[0]}
								{user.familyName?.[0]}
							</AvatarFallback>
						</Avatar>
					</Button>
				</DropdownMenuTrigger>
				<DropdownMenuContent className='dark bg-background ml-2'>
					<DropdownMenuLabel className='font-normal'>
						<div className='flex flex-col space-y-1'>
							<p className='text-sm font-medium leading-none'>{user.name}</p>
							<p className='text-xs leading-none text-muted-foreground'>
								{user.email}
							</p>
						</div>
					</DropdownMenuLabel>

					<DropdownMenuSeparator />

					<DropdownMenuGroup>
						{user.tier !== 'FREE' ? (
							<DropdownMenuItem onClick={openCustomerPortal}>
								Manage subscription
							</DropdownMenuItem>
						) : (
							<DropdownMenuItem onClick={openCheckout}>
								Upgrade
							</DropdownMenuItem>
						)}

						<DropdownMenuItem
							onClick={() => {
								chrome.tabs.create({ url: getSupportEmailLink(user) })
							}}
						>
							Support
						</DropdownMenuItem>
					</DropdownMenuGroup>
					<DropdownMenuSeparator />
					<DropdownMenuItem onClick={logout}>Log out</DropdownMenuItem>
				</DropdownMenuContent>
			</DropdownMenu>

			<MembershipBadge />
		</div>
	)
}

export default AvatarDropdown
