import { Language, parseLanguage } from '@form-solver/common'
import { User } from '@server/user/user.schema'
import { type ClassValue, clsx } from 'clsx'
import { twMerge } from 'tailwind-merge'

export function cn(...inputs: ClassValue[]) {
	return twMerge(clsx(inputs))
}

export const getSupportEmailLink = (user?: User) => {
	const subject = 'Form Solver support request'

	const bodyByLanguage = {
		en: `${user ? `${user.name}, please` : 'Please'} explain here what happened:`,
		id: `${user ? `${user.name}, tolong` : 'Tolong'}, jelaskan apa yang telah terjadi:`,
	} as const satisfies Record<Language, string>

	const body = bodyByLanguage[parseLanguage(user?.language)]

	return `mailto:${import.meta.env.VITE_SUPPORT_EMAIL}?subject=${encodeURIComponent(subject)}&body=${encodeURIComponent(body)}`
}
